<template>
	<div class="wrapper" ref="wrap">
		<div class="content">
			<slot></slot>
		</div>
	</div>
</template>
<script>
	import Bscroll from "better-scroll"
	export default{
		name:"scroll",
		props:{
           probe:{
            type:Number,
            default:0
           },
           pullUpLoad:{
            type:Boolean,
            default:false
           }
		},
		data(){
			return{
				scroll:null
			}
		},
		mounted(){
			this.$nextTick(()=>{
              if(!this.scroll){
				this.scroll = new Bscroll(this.$refs.wrap,{
                 click:true,
                 //0123
                 //this.probe
                 probeType:this.probe,
                 //
                 pullUpLoad:this.pullUpLoad
                })
              }else{
                  this.scroll.refresh();
              }
            
                //
              this.scroll.on('scroll',(position) =>{
                  this.$emit('scroll',position);
              })
                //
              this.scroll.on('pullingUp',()=>{
				this.$emit('pullingUp');
              })
			})
				
			
			
		},
		methods:{
			refresh(){
				//better-scroll
               this.scroll && this.scroll.refresh();
			},
			//time=300time300
			scrollTo(x,y,time=300){
				//better-scroll
               this.scroll && this.scroll.scrollTo(x,y,time);
			},
			finishPullUp(){
				//
				this.scroll && this.scroll.finishPullUp();
			}
		}
	}
</script>
<style scoped></style>